const wa = new Mark(document.querySelector("body"));
function doSearch(text, color, caseSens, diacritic, separate, accurate) {
  wa.mark(`${text}`, {
    accuracy: accurate,
    exclude: ["notifyme", "navigation"],
    element: "hilite",
    iframes: false,
    ignoreJoiners: true,
    ignorePunctuation: [
      ":",
      ";",
      ".",
      ",",
      "-",
      "",
      "",
      "",
      "_",
      "(",
      ")",
      "{",
      "}",
      "[",
      "]",
      "!",
      "'",
      "+",
      "=",
      '"',
    ],
    acrossElements: true,
    separateWordSearch: separate,
    diacritics: diacritic,
    caseSensitive: caseSens,
    each: function (mark) {
      mark.setAttribute(
        "style",
        `background-color: ${color}; border-radius: 5px ;  box-shadow: 0px 0px 2.5px 0px rgba(0, 0, 0, 0.75); `
      );
    },
    done: (done) => {
      if (done > 0) {
        if (yesFind.indexOf(text) == -1 && !noFind.includes(text)) {
          return yesFind.push(text);
        }
      } else if (done <= 0) {
        if (noFind.indexOf(text) == -1 && !yesFind.includes(text)) {
          return noFind.push(text);
        }
      }
    },
  });
}

